.main {
  min-height: var(--main-height);
  background-color: var(--bg);
  width: var(--main-width);
  background-color: var(--bg-secondary);
  border-radius: 4px;
  box-shadow: var(--shadow);
  margin: 0.7rem 0.3rem;
  padding: 0.5rem;
}

.list {
  list-style: none;
  display: flex;
  flex-direction: column;
  background-color: var(--bg-secondary);
  gap: 0.8rem;
  margin: 0.3rem 0;
}

.listitem {
  display: flex;
  background-color: var(--bg-tertiary);
  border-radius: 6px;
  box-shadow: var(--shadow-item);
  min-height: 8vh;
}

.listitem > * {
  padding: 0 0.1rem;
}

.info-menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.titles {
  text-align: left;
  font-size: 1.2rem;
  font-weight: bold;
  box-shadow: none;
}

.no-result {
  text-align: center;
  margin: 0.5rem;
  font-size: 1.1rem;
}

.selected {
  transition: 300ms;
  transform: scale(1.01);
  background-color: var(--bg-tertiary-selected);
  height: fit-content;
}

/* Defines the space that the edit buttons will take*/
.wrapper-edit {
  display: flex;
  flex: 0.5;
}

/* Actual wrapper for the btns and show/hide them on selection */
.wrapper-btns {
  display: none;
  flex: 0.5;
  flex-direction: column;
}

.wrapper-btns-reveal {
  display: flex;
}

.btn {
  cursor: pointer;
  margin: 0.1rem;
  transition: transform 300ms;
  border: none;
  background-color: transparent;
}

.btn:hover {
  transform: scale(1.1);
  transition: transform 300ms;
}

.btn-delete:hover {
  color: var(--red-icon);
}

.wrapper-content {
  display: flex;
  text-align: left;
  align-items: center;
  transition: all 300ms;
  flex: 8.5;
}

.wrapper-content-block {
  display: inline-block;
  text-align: left;
  transition: all 300ms;
  flex: 8.5;
  height: fit-content;
}

.wrapper-content > *,
.wrapper-content-block > * {
  padding: 0 0.1rem;
  height: fit-content;
  word-wrap: break-word;
  width: auto;
  min-width: 10px;
}

.edit {
  width: 80%;
  height: 100%;
  resize: none;
  border-radius: 4px;
  padding: 0.1rem;
  border: 1.5px solid var(--primary);
  font-family: var(--font);
  outline: none;
}

.edit-block {
  width: 90%;
  height: 10%;
  resize: none;
  border-radius: 4px;
  padding: 0.1rem;
  border: 1.5px solid var(--primary);
  font-family: var(--font);
  outline: none;
  margin: 4px;
  font-size: 1.1rem;
}

.edit-example {
  min-height: 20vh;
}
.edit-block-example {
  min-height: 30%;
}

.edit:focus,
.edit-block:focus {
  box-shadow: 0px 0px 6px -1px var(--primary);
  transition: 200ms;
}

.word {
  flex: 1.5;
}

.translation {
  flex: 1.5;
}

.definition {
  flex: 2;
}

.example {
  flex: 2;
}

.pos {
  flex: 1.25;
}

.gloss {
  flex: 0.9;
}

/* Loader */

.wrapper-loader {
  display: flex;
  align-items: center;
  justify-content: center;
}

.wrapper-loader-main {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 60vh;
}

.collabitem {
  border: 2px solid var(--quaternary);
  border-radius: 8px;
}

@media (max-width: 950px) {
  .main {
    width: 100vw;
  }
}

@media (max-width: 850px) {
  .wrapper-content {
    display: inline-block;
  }
  .titles {
    display: none;
  }

}

@media (max-width: 575px) {
  .info-menu {
    flex-direction: column;
  }
}