.page {
  position: fixed;
  top: 50%;
  right: 50%;
  transform: translate(50%, -50%);
  height: 100vh;
  width: 100%;
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  backdrop-filter: blur(5px);
}

.box {
  position: sticky;
  background-color: var(--bg-tertiary);
  border-radius: 8px;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  padding: 2rem;
  box-shadow: var(--shadow);
  font-size: 1.3rem;
  position: relative;
  width: 60vw;
  height: 75vh;
  text-align: center;
  gap: 0.3rem;

}

.btn-close {
  position: absolute;
  top: 0;
  right: 0;
  padding: 0.3rem;
  border: none;
  background-color: var(--red);
  color: white;
  font-size: 1.2rem;
  height: 1.8rem;
  width: 1.8rem;
  text-align: center;
  border-radius: 2px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.btn-close:hover {
  background-color: var(--red-hover);
}
