.wrapper-search {
  display: flex;
  gap: 0.5rem;
  align-items: center;
  border: 2px solid var(--secondary);
  background-color: var(--bg-secondary);
  border-radius: 8px;
  padding: 2px;
  max-width: 200px;
}

.search-bar {
  height: 20px;
  width: 80%;
  font-size: 1rem;
  border-radius: 3px;
  outline: none;
  border: none;
  padding: 2px;
  outline: none;
  font-family: var(--font);
  background-color: var(--bg-secondary);
}

.search-bar::placeholder {
  font-style: italic;
}

.search-icon {
  background-color: var(--bg-secondary);
  transition: transform 200ms;
}
.search-icon:hover {
  transform: scale(1.1);
  transition: transform 400ms;
}

.focus {
  box-shadow: 0px 0px 4px -1px var(--secondary);
  transition: all 100ms linear;
}

.title-filter {
  cursor: default;
  padding: 0.2rem;
  font-size: 1.4rem;
}

@media (max-width:350px) {
  .wrapper-search {
    margin-top: 0.4rem;
  }
}